import React from "react";
import { useLocalStore, useObserver } from "mobx-react";

import { observable, action } from "mobx";
import cobweb from "src/utils/cobweb";

import Table from "@material-ui/core/Table";
import TableBody from "@material-ui/core/TableBody";
import TableCell from "@material-ui/core/TableCell";
import TableHead from "@material-ui/core/TableHead";
import TableRow from "@material-ui/core/TableRow";
import { error } from "src/utils/tools";

class FundListStore {
  @observable
  rows: any[] = [];

  constructor() {
    this.init();
  }

  @action.bound
  async init() {
    try {
      const { cobCore } = await cobweb();
      const roles = ["tech", "market", "prj", "cost"];
      roles.map(async item => {
        try {
          this.rows.push(await cobCore.methods.getFundBy(item).call());
        } catch (err) {
          error(err);
        }
      });
    } catch (error) {}
  }
}

const store = new FundListStore();

const FundList: React.FC = () => {
  const iStore = useLocalStore(() => store);
  return useObserver(() => (
    <>
      <h3>角色列表</h3>
      <Table aria-label="simple table">
        <TableHead>
          <TableRow>
            <TableCell>地址</TableCell>
            <TableCell>角色</TableCell>
            <TableCell>下次从idx开始拿</TableCell>
            <TableCell>已提额</TableCell>
          </TableRow>
        </TableHead>
        <TableBody>
          {iStore.rows.map(row => (
            <TableRow key={row[0]}>
              <TableCell component="th" scope="row">
                {row[0]}
              </TableCell>
              <TableCell>{row[1]}</TableCell>
              <TableCell>{row[2]}</TableCell>
              <TableCell>{row[3]}</TableCell>
            </TableRow>
          ))}
        </TableBody>
      </Table>
    </>
  ));
};

export default FundList;
